import React, { Component } from 'react';
import HomeLayout from '../component/home-layout';
import CuadroContainer from '../../widgets/cuadro-container';
import Setup from '../../widgets/setup';

class Home extends Component {
   state={
       jugar:false,
       perdio:false
   }
   jugar=()=>{
       this.setState({
           jugar:true,
           perdio:false
       })
   }
   perder=()=>{
    this.setState({
        jugar:false,
        perdio:true
    })
   }
    render() {
        
        
        return (
            <HomeLayout>
              {this.state.jugar?<CuadroContainer handledClick={this.perder} />: <Setup titulo={this.state.perdio} 
              boton='Iniciar' handledClick={this.jugar} />}
            </HomeLayout>
        );
    }
}

export default Home;