import React from 'react';
import './setup.css';
const Setup = (props) => {
    return (
        <div className='container-setup'>
            <h1 className='titulo-setup'>Simon says</h1>
            <h2 className='subtitulo-setup'>{props.titulo? 'PERDISTE INTENTALO DE NUEVO': 'JUEGA AHORA'}</h2>
            <button className=" boton-setup btn btn-warning btn-lg" onClick={props.handledClick}>{props.boton}</button>
        </div>
    );
};

export default Setup;