import React, { Component } from 'react';
import Cuadro from './cuadro';
import sonido from './pitido.mp3'
class CuadroContainer extends Component {
    state = {
        indicado: true,
        iterador: 0,
        list: [],
        colores: ['azul', 'amarillo', 'verde', 'rojo'],
        velocidad: 600,
        nivel: 1,
    }
    agregarColores(newList) {

        this.setState({
            list: newList,
        })
        console.log(newList)
    }
    generarSecuencia() {
        const { nivel, colores } = this.state;
        const listaAux = [];
        for (let i = 0; i < nivel; i++) {
            listaAux.push(colores[this.numeroAleatorio(0, 3)]);
        }
        this.agregarColores(listaAux);
    }
    numeroAleatorio(minimo, maximo) {
        return Math.round(Math.random() * (maximo - minimo) + minimo);
    }
    componentDidMount() {
    
        this.generarSecuencia();
        this.iniciar();

    }
    componentWillUnmount(){
        clearInterval(this.timerID);
        clearTimeout(this.thimerInitId);
    }
    iniciar() {
        this.timerID = setInterval(() => {
            if (this.state.indicado) {
                this.indicarColor(this.state.list[this.state.iterador]);
                this.setState({
                    indicado: false
                })
            } else {
                this.restablecerColor(this.state.list[this.state.iterador]);
                this.setState({
                    indicado: true
                })
            }
        }, this.state.velocidad);

    }
    indicarColor = color => {
        switch (color) {
            case 'azul':
                this.azul.classList.add('opacity');
                this.pitido.play()
                break;
            case 'verde':
                this.verde.classList.add('opacity');
                this.pitido.play()
                break;
            case 'amarillo':
                this.amarillo.classList.add('opacity');
                this.pitido.play()
                break;
            case 'rojo':
                this.rojo.classList.add('opacity');
                this.pitido.play()
                break;

            default:
                break;
        }

    }
    restablecerColor = color => {
        switch (color) {
            case 'azul':
                this.azul.classList.remove('opacity');
                break;
            case 'verde':
                this.verde.classList.remove('opacity');
                break;
            case 'amarillo':
                this.amarillo.classList.remove('opacity');
                break;
            case 'rojo':
                this.rojo.classList.remove('opacity');
                break;

            default:
                break;
        }
        clearInterval(this.timerID);
        if (this.state.iterador + 1 < this.state.nivel) {
            this.iniciar();
            this.iteradorSiguiente();
        } else {
            this.inicializarIterador();
        }

    }
    iteradorSiguiente() {
        this.setState(
            {
                iterador: this.state.iterador + 1
            }
        )
    }
    inicializarIterador() {
        this.setState(
            {
                iterador: 0
            }
        )
    }

    escogeColor = (ev) => {
        this.pitido.play();
        let color = ev.target.getAttribute('data-value');//
        (this.comparar(color, this.state.list[this.state.iterador])) ?
            this.siguienteColor() :
            this.props.handledClick()
    }
    comparar = (color, colorVerdadero) => {
        return color === colorVerdadero ? true : false
    }
    siguienteColor() {
        const { nivel, velocidad } = this.state;
        if (this.state.iterador + 1 < this.state.nivel) {
            this.iteradorSiguiente();
        } else {
            this.setState({
                nivel: nivel + 1,
                iterador: 0,
                list: [],
            })
            this.thimerInitId = setTimeout(() => {
                this.generarSecuencia();
                this.iniciar()
            }, 1500);
        }

    }

    render() {
        return (
            <div>
                <h1 className='score'> SCORE: {this.state.nivel}</h1>
                <div className='forma-circulo'>
                    <div className='cuadro-container'>
                        <Cuadro valor='azul' handledClick={this.escogeColor} color='azul' setRef={this.setCuadroAzulRef}></Cuadro>
                        <Cuadro valor='amarillo' handledClick={this.escogeColor} color='amarillo' setRef={this.setCuadroAmarilloRef}></Cuadro>
                    </div>

                    <div className='cuadro-container'>
                        <Cuadro valor='rojo' handledClick={this.escogeColor} color='rojo' setRef={this.setCuadroRojoRef} ></Cuadro>
                        
                        <Cuadro valor='verde' handledClick={this.escogeColor} color='verde' setRef={this.setCuadroVerdeRef}></Cuadro>
                    </div>
                    <audio ref={this.setPitidoRef} >
                        <source src={sonido}></source>
                    </audio>
                </div>
            </div>

        );
    }
    setCuadroAzulRef = element => {
        this.azul = element;
    }
    setPitidoRef = element => {
        this.pitido = element;
    }
    setCuadroAmarilloRef = element => {
        this.amarillo = element;
    }
    setCuadroRojoRef = element => {
        this.rojo = element;
    }
    setCuadroVerdeRef = element => {
        this.verde = element;
    }
}

export default CuadroContainer;